@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:'Poppins', sans-serif}body{background-color:#E5E5E5}


.cv-toolbar{width:100%;padding:17px 24px;height:60px;background-color:#fff;border-bottom:2px solid #cccccc}.cv-toolbar .cv-brand{font-size:18px;font-weight:600;color:#109CF1}@media screen and (max-width: 732px){.cv-toolbar .cv-brand{margin-left:0;font-size:24px}}

.cv-home{padding:34px}.cv-home .cv-row{display:grid;justify-content:center;grid-template-columns:1fr 1fr 1fr;grid-column-gap:32px}.cv-home .cv-row .cv-card{border-radius:4px;padding:24px;box-shadow:0px 6px 18px rgba(0,0,0,0.06);background-color:#fff !important}.cv-home .cv-row .cv-card p{padding:8px 0;font-weight:600}@media screen and (max-width: 732px){.cv-home .cv-row{grid-template-columns:1fr;grid-row-gap:32px}}@media screen and (max-width: 732px){.cv-home{padding:12px}}

